import React from 'react';
import { Cover } from './Cover'; // Assicurati che il percorso sia corretto

export default {
  title: 'Example/Atoms/Cover', // Nome della storia
  component: Cover, // Il componente da visualizzare
  argTypes: {
    src: { control: 'text' }, // Permette di inserire un URL per l'immagine
    alt: { control: 'text' }, // Testo alternativo per l'immagine
    width: { control: 'text' }, // Larghezza dell'immagine
    height: { control: 'text' }, // Altezza dell'immagine
  },
};

const Template = (args) => <Image {...args} />;

export const Default = Template.bind({});
Default.args = {
  src: 'https://via.placeholder.com/300', // URL dell'immagine di default
  alt: 'Placeholder Image', // Testo alternativo di default
  width: '300px', // Larghezza dell'immagine di default
  height: 'auto', // Altezza dell'immagine di default
};

export const CustomSize = Template.bind({});
CustomSize.args = {
  src: 'https://via.placeholder.com/600x400', // URL dell'immagine personalizzata
  alt: 'Custom Size Image', // Testo alternativo per l'immagine personalizzata
  width: '500px', // Larghezza personalizzata
  height: 'auto', // Altezza personalizzata
};